"use strict";

var React = React;

var BrowserFolderContentItem = React.createClass({
	displayName: 'BrowserFolderContentItem',

	propTypes: {
		content: React.PropTypes.object.isRequired,
		selectedItemId: React.PropTypes.string,
		onSelectedItemChanged: React.PropTypes.func.isRequired,
		onSelectedFolderChanged: React.PropTypes.func.isRequired
	},
	isSelected: function isSelected() {
		return this.props.content.id === this.props.selectedItemId;
	},
	onClick: function onClick() {

		this.props.onSelectedItemChanged(this.props.content);
	},
	onDoubleClick: function onDoubleClick() {

		if (!this.props.content.isFile) {
			this.props.onSelectedFolderChanged(this.props.content);
		}
	},
	render: function render() {

		var c = this.props.content,
		    fileTypeClass = !c.isFile ? ' item-folder' : ' item-file',
		    isImageClass = c.isImage ? ' item-image' : '',
		    isSelected = this.isSelected() ? ' selected' : '',
		    title = !c.isFile ? c.path : c.link;

		return React.createElement(
			'div',
			{ className: "item " + fileTypeClass + isImageClass + isSelected, title: title, onClick: this.onClick, onDoubleClick: this.onDoubleClick },
			React.createElement(
				'div',
				{ className: 'item-icon' },
				React.createElement('span', null),
				React.createElement('img', { src: c.icon })
			),
			React.createElement(
				'div',
				{ className: 'item-caption' },
				React.createElement(
					'span',
					null,
					c.name
				)
			)
		);
	}
});